package com.jonnyzzz.logo.web.i18

import react.RBuilder

object Messages {

  object Titles {
    val jogo = "Jogo"
  }
}

data class Message(
  val en: String,
  val ru: String = en,
  val de: String = en
) {
  override fun toString() = en
}

class RMessageBuilder(val builder: RBuilder)

operator fun RMessageBuilder.invoke(
  message: Message
) {
  builder.run {
    //TODO: use state and current language
    + message.toString()
  }
}

object X

operator fun RMessageBuilder.invoke(
  doNotUseDefaultParameters: X = X,
  en: String,
  ru: String = en,
  de: String = en) = this(Message(en, ru, de))

val RBuilder.msg get() = RMessageBuilder(this)
