package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.script.*
import com.jonnyzzz.logo.web.custom
import com.jonnyzzz.logo.web.reducers.ScriptBlock
import com.jonnyzzz.logo.web.reducers.State
import kotlinx.html.HTMLTag
import react.*
import react.dom.RDOMBuilder
import react.dom.svg
import react.redux.rConnect

interface SvgWorldPublicProps : RProps {
}

interface SvgWorldProps : SvgWorldPublicProps, RProps {
  var world: ScriptWorld
  var scripts: List<ScriptBlock>
}

val SvgWorldMainComponent: RClass<SvgWorldPublicProps> =
  rConnect<State, SvgWorldPublicProps, SvgWorldProps>(
    mapStateToProps = { state, pub ->
      scripts = state.scripts.filter { it.active }
      world = state.world
    }
  ).invoke(SvgWorld::class.js.unsafeCast<RClass<SvgWorldProps>>())

val SvgWorldComponent: RClass<SvgWorldProps> =
  SvgWorld::class.js.unsafeCast<RClass<SvgWorldProps>>()

private class SvgWorld(props: SvgWorldProps) : RComponent<SvgWorldProps, RState>(props) {
  override fun RBuilder.render() {
    val world = props.world
    svg {
      // https://developer.mozilla.org/en-US/docs/Web/SVG/Attribute/viewBox
      // minX, minY, width, height
      attrs["width"] = "100%"
      attrs["viewBox"] = world.toViewBox()

      val player = WorldPlayer(world)
      var state = player.newSnapshot()

      for (script in props.scripts) {

        val commands = processScript(script.script)
        val worldSnapshot = player.play(commands, state)
        state = worldSnapshot

        for (jSvg in worldSnapshot.svg) {
          toSvg(jSvg) {
            attrs["strokeWidth"] = if (script.hover) "4px" else "2px"
            attrs["stroke"] = "black"
          }
        }
      }

      jogoHead(state)
    }
  }
}

private fun ScriptWorld.toViewBox() = "${-sizeX / 2} ${-sizeY / 2} $sizeX $sizeY"

fun RBuilder.jogoIcon(state: ScriptState = ScriptState(), size: Int = 1) {
  svg {
    val world = ScriptWorld(32, 32)
    attrs["width"] = "${32 * size}px"
    attrs["height"] = "${32 * size}px"
    // https://developer.mozilla.org/en-US/docs/Web/SVG/Attribute/viewBox
    // minX, minY, width, height
    attrs["viewBox"] = world.toViewBox()
    jogoHead(state, 10)
  }
}

private fun RBuilder.jogoHead(state: WorldSnapshot) {
  jogoHead(state.finalState, state.world.tSize)
}

private fun RBuilder.jogoHead(pos: ScriptState, tSize: Int) {
  custom("circle") {
    attrs["cx"] = pos.x
    attrs["cy"] = pos.y
    attrs["r"] = tSize
    attrs["style"] = object {
      val stroke = "black"
      val fill = "red"
    }
  }

  custom("line") {
    attrs["x1"] = pos.x
    attrs["y1"] = pos.y
    attrs["x2"] = pos.x + tSize * pos.dX
    attrs["y2"] = pos.y + tSize * pos.dY
    attrs["strokeWidth"] = "2px"
    attrs["stroke"] = "black"
  }
}

private fun RBuilder.toSvg(svg: JSvgElement, extra: RDOMBuilder<HTMLTag>.() -> Unit) {
  when (svg) {
    is JSvgLineElement -> toSvg(svg, extra)
    is JSvgCircleElement -> toSvg(svg, extra)
    else -> error("Unknown element: $svg")
  }
}

private fun RBuilder.toSvg(svg: JSvgLineElement, extra: RDOMBuilder<HTMLTag>.() -> Unit) {
  custom("line") {
    attrs["x1"] = svg.from.x
    attrs["y1"] = svg.from.y
    attrs["x2"] = svg.to.x
    attrs["y2"] = svg.to.y
    extra()
  }
}

private fun RBuilder.toSvg(svg: JSvgCircleElement, extra: RDOMBuilder<HTMLTag>.() -> Unit) {
  custom("circle") {
    //TODO: use path, see https://svg-path-visualizer.netlify.app/ for details on A/a commands
    // https://www.w3.org/TR/SVG2/paths.html#PathDataEllipticalArcCommands
    //attrs["path"] = buildString {
    //  append("M ${state.x} ${state.y} ")
    //  append("a ${scriptCommand.radius} ${scriptCommand.radius} ${state.headAngle} ")
    //}
    attrs["r"] = svg.radius
    attrs["cx"] = svg.center.x
    attrs["cy"] = svg.center.y
    attrs["fill"] = "none"
    extra()
  }
}
