package com.jonnyzzz.logo.web.reducers

import com.jonnyzzz.logo.script.ScriptWorld
import com.jonnyzzz.logo.web.newUuid
import kotlinx.serialization.Serializable
import kotlin.math.sqrt

@Serializable
data class State(
        val world: ScriptWorld = ScriptWorld(300, 300),

        val scripts: List<ScriptBlock> = listOf(
                ScriptBlock("fd 40\nlt 90"),
                ScriptBlock("fd 40\nlt " + (90 + 45)),
                ScriptBlock("fd " + sqrt(0.0 + 40 * 40 + 40 * 40).toInt()),
                ScriptBlock("pu\nfd 90\npd"),
        )
)

@Serializable
data class ScriptBlock(
        val script: String,
        val hover: Boolean = false,
        val active: Boolean = true,
        val id: String = newUuid(),
)
