package com.jonnyzzz.logo.web.reducers

import kotlinx.browser.localStorage
import kotlinx.serialization.*
import kotlinx.serialization.json.Json
import org.w3c.dom.get
import org.w3c.dom.set
import redux.RAction
import redux.compose
import redux.createStore
import redux.rEnhancer


private fun reducer(state: State, action: RAction): State = state.run {
  return when (action) {
    is StateAction -> action.run { update() }
    else -> state
  }
}

private val localStorageKey = "jonnyzzz-jogo"

private fun loadState(): State {
  runCatching {
    val stateText = localStorage[localStorageKey] ?: ""
    val state = Json.decodeFromString<State>(stateText)
    return state.copy(scripts = state.scripts.map { it.copy(hover = false) })
  }

  return State()
}

@Suppress("UnsafeCastFromDynamic")
val store = createStore<State, RAction, dynamic>(
        ::reducer,
        //https://github.com/lawik123/kotlin-poc-frontend-react-redux/issues/3#issuecomment-754718637
        loadState(),
        compose(
                rEnhancer(),
                js("if(window.__REDUX_DEVTOOLS_EXTENSION__ )window.__REDUX_DEVTOOLS_EXTENSION__ ();else(function(f){return f;});")
        )).apply {
  subscribe {
    localStorage[localStorageKey] = Json.encodeToString(getState())
  }
}
