package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.reducers.store
import kotlinx.browser.document
import kotlinx.css.*
import react.RBuilder
import react.dom.br
import react.dom.div
import react.dom.render
import react.redux.provider
import styled.css
import styled.styledDiv

fun main() {
  val rootDiv = document.getElementById("root")
  render(rootDiv) {
    provider(store) {
      mainLayout {
        app()
      }

      styledDiv {
        css {
          fontSize = 80.pct
          textAlign = TextAlign.center
        }

        + "© Eugene Petrenko 2021. For more information, please contact me at jonnyzzz.com."
        br {}
        + "Powered by Kotlin/JS with React"
      }
    }
  }
}

private fun RBuilder.mainLayout(contents: RBuilder.() -> Unit) {
  styledDiv {
    css {
      width = 80.pct
      minWidth = 500.px
      margin(LinearDimension("0"), LinearDimension.auto)
      textAlign = TextAlign.center

      child("div") {
        textAlign = TextAlign.left
      }
    }

    div {
      contents()
    }
  }
}
