package com.jonnyzzz.logo.web.reducers

import redux.RAction


interface StateAction : RAction {
  fun State.update(): State
}

data class ScriptAddAction(
        val script: String
) : StateAction {
  override fun State.update(): State = copy(scripts = scripts + ScriptBlock(script))
}

class ScriptResetAllAction: StateAction {
  override fun State.update(): State = State()
}

class ScriptClearAllAction: StateAction {
  override fun State.update(): State = copy(scripts = listOf())
}

data class ScriptDupAction(
        val scriptBlockId: String
) : StateAction {
  override fun State.update(): State {
    val block = scripts.single { it.id == scriptBlockId }
    val new = ScriptBlock(block.script)
    return copy(scripts = scripts + new)
  }
}

data class ScriptBlockHoverAction(
        val isHover: Boolean,
        val scriptBlockId: String,
) : StateAction {
  override fun State.update(): State = copy(scripts = scripts.map { block ->
    if (block.id == scriptBlockId) {
      block.copy(hover = isHover)
    } else {
      block
    }
  })
}

data class ScriptBlockActiveAction(
        val isActive: Boolean,
        val scriptBlockId: String,
) : StateAction {
  override fun State.update(): State = copy(scripts = scripts.map { block ->
    if (block.id == scriptBlockId) {
      block.copy(active = isActive)
    } else {
      block
    }
  })
}

data class ScriptBlockDeleteAction(
        val scriptBlockId: String,
) : StateAction {
  override fun State.update(): State = copy(scripts = scripts.filterNot { block ->
    block.id == scriptBlockId
  })
}
