package com.jonnyzzz.logo.web

import kotlinx.html.HTMLTag
import react.RBuilder
import react.ReactElement
import react.dom.RDOMBuilder
import react.dom.tag

// https://stackoverflow.com/a/50354600/49811
// a custom tag builder, reuses the tag(...) function from kotlin-react and HTMLTag from kotlinx.html
inline fun RBuilder.custom(
        tagName: String,
        block: RDOMBuilder<HTMLTag>.() -> Unit
): ReactElement = tag(block) {
  HTMLTag(tagName = tagName,
          consumer = it,
          initialAttributes = mapOf(),
          namespace = null,
          inlineTag = true,
          emptyTag = false) // I dont know yet what the last 3 params mean... to lazy to look it up
}
