package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.asRClass
import com.jonnyzzz.logo.web.i18.invoke
import com.jonnyzzz.logo.web.i18.msg
import com.jonnyzzz.logo.web.pushHelp
import com.jonnyzzz.logo.web.reducers.ScriptClearAllAction
import com.jonnyzzz.logo.web.reducers.ScriptResetAllAction
import kotlinx.css.padding
import kotlinx.css.px
import kotlinx.html.DIV
import kotlinx.html.js.onClickFunction
import react.*
import react.dom.RDOMBuilder
import react.dom.a
import react.redux.rConnect
import react.router.dom.useHistory
import redux.RAction
import redux.WrapperAction
import styled.css
import styled.styledDiv

private interface MainMenuProps : RProps {
  var onReset: () -> Unit
  var onClear: () -> Unit
}

val MainMenuComponent: RClass<RProps> =
  rConnect<RAction, WrapperAction, RProps, MainMenuProps>(
    mapDispatchToProps = { dispatch, _ ->
      onReset = { dispatch(ScriptResetAllAction()) }
      onClear = { dispatch(ScriptClearAllAction()) }
    }
  ).invoke(MainMenu::class.asRClass())

private class MainMenu : RComponent<MainMenuProps, RState>() {
  override fun RBuilder.render() {
    styledDiv {
      css {
        child("a") {
          padding(10.px)
        }
      }
      resetLink()
      clearLink()
      child(HelpLinkComponent)
    }
  }

  private fun RDOMBuilder<DIV>.clearLink() {
    a(href = "#", classes = "menu-link") {
      attrs.onClickFunction = {
        it.preventDefault()
        props.onClear()
      }

      msg(
        en = "Clean Script",
        de = "Program Löschen",
        ru = "Удалить программу"
      )
    }
  }

  private fun RDOMBuilder<DIV>.resetLink() {
    a(href = "#", classes = "menu-link") {
      attrs.onClickFunction = {
        it.preventDefault()
        props.onReset()
      }

      msg(
        en = "Restart",
        de = "Neue Start",
        ru = "Начать заново"
      )
    }
  }
}

private val HelpLinkComponent = functionalComponent<RProps> {
  val history = useHistory()

  a(href = "#/help", classes = "menu-link") {
    attrs.onClickFunction = {
      it.preventDefault()
      history.pushHelp()
    }

    msg(
      en = "Help",
      de = "Hilfe",
      ru = "Помощь"
    )
  }
}
