package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.components.*
import com.jonnyzzz.logo.web.i18.Messages
import kotlinx.css.*
import kotlinx.css.properties.border
import react.RBuilder
import react.dom.h1
import react.dom.sup
import react.router.dom.LinkComponent
import styled.css
import styled.styledDiv

fun RBuilder.studio() {
  h1 {
    + Messages.Titles.jogo

    sup {
      child(LinkComponent::class) {
        attrs {
          to = helpPath
        }
        svgIcon(SvgIcon.HELP)
      }
    }
  }

  styledDiv {
    MainMenuComponent { }
  }

  styledDiv {
    css {
      marginBottom = 25.px

      width = min(70.vh, 70.vw)
      height = min(70.vh, 70.vw)
      border(2.px, BorderStyle.solid, Color.brown, 5.px)
    }

    SvgWorldMainComponent { }
  }

  styledDiv {
    css.clear = Clear.both
  }

  ScriptAddFormComponent {
    attrs.defaultText = ""
  }

  styledDiv {
    css.clear = Clear.both
  }

  ScriptsViewComponent {}

  styledDiv {
    css.clear = Clear.both
  }
}
