package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.script.ScriptWorld
import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.i18.Message
import com.jonnyzzz.logo.web.i18.invoke
import com.jonnyzzz.logo.web.i18.msg
import com.jonnyzzz.logo.web.reducers.ScriptBlock
import kotlinx.css.*
import kotlinx.css.properties.borderTop
import kotlinx.html.TABLE
import react.RBuilder
import react.dom.*
import styled.StyledDOMBuilder
import styled.css
import styled.styledDiv
import styled.styledTable

fun RBuilder.commands() {
  commandsList {
    doc(
      command = "home",
      explanation = Message(
        en = "Move Jogo to the center of the screen"
      )
    )

    doc(
      command = "head <N>",
      example = "head 45",
      explanation = Message(
        en = "Turns Jogo to the absolute angle, use 0 to turn it up"
      )
    )

    doc(
      command = "fd <N>",
      example = "fd 40",
      explanation = Message(
        en = "Move Jogo forward to 40 steps. It goes towards head direction"
      )
    )

    doc(
      command = "lt <N>",
      example = "lt 30",
      explanation = Message(
        en = "Turns Jogo to the left on 30 degrees"
      )
    )

    doc(
      command = "rt <N>",
      example = "rt 30",
      explanation = Message(
        en = "Turns Jogo to the right on 30 degrees"
      )
    )

    doc(
      command = "arc <N>, 360",
      example = "arc 27, 360",
      explanation = Message(
        en = "Make Jogo to draw a circle with the center in the current point"
      )
    )
  }

  br {}

  div {
    msg(
      en = "Legend",
      de = "Beschriftung",
      ru = "Сокращения"
    )
  }

  shortsList {
    shortEntry(
      "<N>",
      "100",
      Message(
        en = "means a number",
        de = "bedauted eine Nummber",
        ru = "число",
      ),
    )
  }
}

private fun RBuilder.code(code: String) {
  pre {
    +code
  }
}

private fun RBuilder.shortEntry(
  code: String,
  example: String?,
  explanation: Message,
) {
  tr {
    td("code") {
      code(code)
    }
    td("exp") {
      msg(explanation)
      if (example != null) {
        br { }
        code(example)
      }
    }
  }
}

private fun RBuilder.shortsList(
  contents: RBuilder.() -> Unit
) {
  styledTable {
    tableCss()
    thead {
      tr {
        th {
          msg(
            en = "Command"
          )
        }
        th {
          msg(
            en = "Description"
          )
        }
      }
    }
    tbody {
      contents()
    }
  }
}

private fun StyledDOMBuilder<TABLE>.tableCss() {
  css {
    child("thead") {
      fontWeight = FontWeight.bold
    }

    child("tbody") {
      child("tr") {
        paddingTop = 5.px
        paddingBottom = 5.px
        borderTop(1.px, BorderStyle.solid, Color.lightSlateGray)
      }

      child("tr") {
        child("td.code") {
          paddingRight = 50.px
          verticalAlign = VerticalAlign.top

          child("pre") {
            JogoStyle.jetbrainsMono(this)
          }
        }

        child("td span.arrow") {
          JogoStyle.jetbrainsMono(this)
          fontWeight = FontWeight.bold
          fontSize = 130.pct
          verticalAlign = VerticalAlign.middle
          height = 50.px
        }
      }
    }
  }
}


private fun RBuilder.commandsList(
  contents: RBuilder.() -> Unit
) {
  br {}
  styledTable {
    tableCss()
    thead {
      tr {
        th {
          msg(
            en = "Command"
          )
        }
        th {
          msg(
            en = "Description"
          )
        }
      }
    }
    tbody {
      contents()
    }
  }
}

private fun RBuilder.doc(
  command: String,
  example: String? = null,
  explanation: Message
) {
  tr {
    td(classes = "code") {
      code(command)
    }

    td(classes = "exp") {
      msg(explanation)

      if (example != null) {
        br {}
        svgWorld(50, 50)

        span(classes = "arrow") {
          +" -> "
        }
        svgWorld(50, 50, example)
      }
    }
  }
}

private fun RBuilder.svgWorld(width: Int, height: Int, vararg script: String) {
  styledDiv {
    css {
      this.width = width.px
      this.height = height.px
      this.display = Display.inlineBlock
      this.margin(5.px)

      child("svg") {
        this.width = width.px
        this.height = height.px
      }
    }

    SvgWorldComponent {
      attrs.world = ScriptWorld(2 * width, 2 * height, tSize = 20)
      attrs.scripts = listOf(ScriptBlock(script.joinToString("\n")))
    }
  }
}
