package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.reducers.ScriptBlock
import com.jonnyzzz.logo.web.reducers.State
import kotlinx.css.Overflow
import kotlinx.css.overflowY
import react.*
import react.redux.rConnect
import styled.css
import styled.styledDiv

private interface ScriptsViewProps : RProps {
  var scriptBlocks: List<ScriptBlock>
}

val ScriptsViewComponent: RClass<RProps> =
        rConnect<State, RProps, ScriptsViewProps>(
                mapStateToProps = { state, _ ->
                  scriptBlocks = state.scripts
                }
        ).invoke(ScriptsView::class.js.unsafeCast<RClass<ScriptsViewProps>>())

private class ScriptsView(props: ScriptsViewProps) : RComponent<ScriptsViewProps, RState>(props) {
  override fun RBuilder.render() {
    styledDiv {
      css {
        overflowY = Overflow.scroll
      }

      props.scriptBlocks.reversed().renderEach { block ->
        ScriptViewComponent {
          attrs.key = block.id
          attrs.scriptBlockId = block.id
        }
      }
    }
  }
}
