package com.jonnyzzz.logo.script

sealed class ScriptCommand {
  abstract fun updateState(state: ScriptState) : ScriptState
}

data class ScriptArcCommand(val radius: Int, val angle: Int) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState = state
}

data class ScriptMoveCommand(val size: Int) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    val pX = size * state.dX
    val pY = size * state.dY
    return state.copy(point = ScriptPoint(x = state.x + pX.toInt(), y = state.y + pY.toInt()))
  }
}

data class ScriptTurnCommand(val angle: Int) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    var newAngle = (state.headDirection + angle) % 360
    if (newAngle < 0) newAngle += 360
    return state.copy(headDirection = newAngle)
  }
}

object ScriptJumpHomeCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(point = ScriptPoint(x = 0, y = 0), headDirection = 0)
  }

  override fun toString() = "ScriptJumpHomeCommand"
}

data class ScriptHeadTurnCommand(val angle: Int) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(headDirection = angle)
  }
}

object ScriptPenUpCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penIsOn = false)
  }
  override fun toString() = "ScriptPenUpCommand"
}

object ScriptPenDownCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penIsOn = true)
  }
  
  override fun toString() = "ScriptPenDownCommand"
}
