package com.jonnyzzz.logo.script

sealed class JSvgElement

data class JSvgLineElement(
  val from: ScriptPoint,
  val to: ScriptPoint
) : JSvgElement()

data class JSvgCircleElement(
  val center: ScriptPoint,
  val radius: Int
) : JSvgElement()

data class WorldSnapshot(
  val player: WorldPlayer,

  val startState: ScriptState,
  val finalState: ScriptState,

  val svg: List<JSvgElement>,
) {
  val world get() = player.world
}

class WorldPlayer(
  val world: ScriptWorld
) {
  fun newSnapshot() = WorldSnapshot(this, ScriptState(), ScriptState(), listOf())

  fun play(commands: List<ScriptCommand>,
           start : WorldSnapshot = newSnapshot()
  ) : WorldSnapshot {
    val svg = mutableListOf<JSvgElement>()

    var state = start.finalState
    for (scriptCommand in commands) {
        val newState = scriptCommand.updateState(state)

        if (state.penIsOn) {
          if (scriptCommand is ScriptArcCommand) {
            //TODO: use path, see https://svg-path-visualizer.netlify.app/ for details on A/a commands
            // https://www.w3.org/TR/SVG2/paths.html#PathDataEllipticalArcCommands
            //attrs["path"] = buildString {
            //  append("M ${state.x} ${state.y} ")
            //  append("a ${scriptCommand.radius} ${scriptCommand.radius} ${state.headAngle} ")
            //}
            svg += JSvgCircleElement(state.point, scriptCommand.radius)
          } else if (newState.point != state.point) {
            svg += JSvgLineElement(state.point, newState.point)
          }
        }

        state = newState
      }

    return WorldSnapshot(this, start.finalState, state, svg)
  }
}
