package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.reducers.*
import kotlinx.css.*
import kotlinx.html.CommonAttributeGroupFacade
import kotlinx.html.DIV
import kotlinx.html.js.onClickFunction
import kotlinx.html.js.onTouchStartFunction
import org.w3c.dom.events.Event
import react.*
import react.dom.a
import react.dom.div
import react.redux.rConnect
import redux.RAction
import redux.WrapperAction
import styled.StyledDOMBuilder
import styled.css
import styled.styledDiv

interface ScriptViewPublicProps : RProps {
  var scriptBlockId: String
}

private interface ScriptViewProps : RProps {
  var scriptBlock: ScriptBlock

  var mouseIn: () -> Unit
  var mouseOut: () -> Unit

  var toggleActive: (active: Boolean) -> Unit
  var delete: () -> Unit
  var duplicate: () -> Unit
}

val ScriptViewComponent: RClass<ScriptViewPublicProps> =
  rConnect<State, RAction, WrapperAction, ScriptViewPublicProps, ScriptViewProps, ScriptViewProps, ScriptViewProps>(
    mapStateToProps = { state, p ->
      scriptBlock = state.scripts.single { it.id == p.scriptBlockId }
      key = scriptBlock.id
    },
    mapDispatchToProps = { dispatch, p ->
      mouseIn = { dispatch(ScriptBlockHoverAction(true, p.scriptBlockId)) }
      mouseOut = { dispatch(ScriptBlockHoverAction(false, p.scriptBlockId)) }
      toggleActive = { dispatch(ScriptBlockActiveAction(it, p.scriptBlockId)) }
      delete = { dispatch(ScriptBlockDeleteAction(p.scriptBlockId)) }
      duplicate = { dispatch(ScriptDupAction(p.scriptBlockId)) }
    }
  ).invoke(ScriptView::class.js.unsafeCast<RClass<ScriptViewProps>>())


private class ScriptView(props: ScriptViewProps) : RComponent<ScriptViewProps, RState>(props) {
  override fun RBuilder.render() {
    styledDiv {
      css {
        marginBottom = 7.px
        marginTop = 7.px
        display = Display.flex

        child("div") {
          flex()
        }

        when {
          props.scriptBlock.hover -> backgroundColor = Color.aliceBlue
          !props.scriptBlock.active -> backgroundColor = Color.lightCyan
        }

        child("div.links") {
          paddingLeft = 25.px

          child("a") {
            padding(10.px)
          }
        }
      }

      attrs.onMouseEnterFunction = { props.mouseIn() }
      attrs.onMouseLeaveFunction = { props.mouseOut() }

      attrs.onTouchStartFunction = { if (!props.scriptBlock.hover) props.mouseIn() else props.mouseOut() }
      attrs.onClickFunction = { if (!props.scriptBlock.hover) props.mouseIn() else props.mouseOut() }

      div(classes = "editor") {
        scriptEditorComponent {
          readonly = true
          text = props.scriptBlock.script
        }
      }

      div(classes = "links") {
        controlLinks()
      }
    }

    styledDiv {
      css.clear = Clear.both
    }
  }

  private fun RBuilder.controlLinks() {
    a(href = "#") {
      attrs.onClickFunction = {
        it.preventDefault()
        props.toggleActive(!props.scriptBlock.active)
      }
      if (props.scriptBlock.active) {
        +"Hide"
      } else {
        +"Show"
      }
    }

    a(href = "#") {
      attrs.onClickFunction = {
        it.preventDefault()
        props.delete()
      }
      +"Delete"
    }

    a(href = "#") {
      attrs.onClickFunction = {
        it.preventDefault()
        props.duplicate()
      }
      +"Duplicate"
    }
  }
}

private var CommonAttributeGroupFacade.onMouseEnterFunction: (Event) -> Unit
  get() = throw UnsupportedOperationException("You can't read variable onMouseEnterFunction")
  set(newValue) {
    consumer.onTagEvent(this, "onmouseenter", newValue)
  }

private var CommonAttributeGroupFacade.onMouseLeaveFunction: (Event) -> Unit
  get() = throw UnsupportedOperationException("You can't read variable onMouseLeaveFunction")
  set(newValue) {
    consumer.onTagEvent(this, "onmouseleave", newValue)
  }
