package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.i18.invoke
import com.jonnyzzz.logo.web.i18.msg
import kotlinx.css.*
import react.RBuilder
import react.dom.br
import react.dom.span
import styled.css
import styled.styledDiv
import styled.styledImg

fun RBuilder.notFound() {
  styledDiv {
    css {
      textAlign = TextAlign.center
      paddingTop = 50.px
      paddingLeft = 50.px
      margin = "0 auto"
      JogoStyle.jetbrainsMono(this)
      fontSize = 200.pct

      child("span") {
        padding(20.pct)
      }
    }

    span {
      msg(
        en = "404. Not found",
      )
    }

    br {}

    styledImg(src = "https://jonnyzzz.com/images/404.jpg") {
      css {
        width = 45.pct
      }
    }

    br {}
  }

}
