package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.components.notFound
import com.jonnyzzz.logo.web.components.scriptReadme
import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.i18.invoke
import com.jonnyzzz.logo.web.i18.msg
import kotlinx.browser.window
import kotlinx.css.*
import kotlinx.html.js.onClickFunction
import react.*
import react.dom.div
import react.router.dom.*
import styled.css
import styled.styledA
import styled.styledDiv

fun RBuilder.app() = styledDiv {
  JogoStyle.html(this)

  hashRouter {
    switch {
      route("", exact = true) {
        div {
          child(ScrollToTop)
          studio()

          styledDiv {
            css {
              height = 25.px
            }
          }
        }
      }

      route(helpPath, exact = true) {
        div {
          child(ScrollToTop)
          historyBackLink()

          scriptReadme()
        }
      }

      route("*") {
        div {
          child(ScrollToTop)
          notFound()
        }
      }
    }
  }
}

fun RBuilder.historyBackLink() {
  child(historyBackLink)
}

val helpPath = "/help"
fun RouteResultHistory.pushHelp() = push(helpPath)

private val historyBackLink = functionalComponent<RProps>{
  val routerHistory = useHistory()

  div {
    styledA(href = "") {
      css {
        fontWeight = FontWeight.bold
      }

      attrs.onClickFunction = {
        it.preventDefault()
        routerHistory.goBack()
      }
      
      +"<< "
      msg(
        en = "Back",
        de = "Zurück",
        ru = "Назад",
      )
    }
  }
}


private val ScrollToTop = functionalComponent<RProps> {
  val pathname = useLocation().pathname
  useEffect(dependencies = listOf(pathname)) {
    window.scrollTo(0.0, 0.0)
  }
}
