package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.i18.Messages
import com.jonnyzzz.logo.web.i18.invoke
import com.jonnyzzz.logo.web.i18.msg
import react.RBuilder
import react.dom.br
import react.dom.div
import react.dom.h1

fun RBuilder.scriptReadme() {
  div {
    h1 {
      +Messages.Titles.jogo
    }

    msg(
      en = "Hello, I'm Jogo.",
      de = "Hallo, ich bin Jogo.",
      ru = "Привет, я Жого."
    )

    br {}

    jogoIcon(size = 4)

    br {}

    msg(
      en = "You may control me with the text commands",
      ru = "Ты можешь управлять мной через команды"
    )

    br {}

    commands()
  }
}

