package com.jonnyzzz.logo.web.components

import kotlinx.css.height
import kotlinx.css.px
import kotlinx.css.width
import react.*
import styled.css
import styled.styledSpan

@JsModule("icons/help.svg")
private external object HelpIconWrapper {

  @JsName("default")
  class HelpIcon : Component<dynamic, dynamic> {
    override fun render(): ReactElement?
  }
}

enum class SvgIcon {
  HELP,
}

fun RBuilder.svgIcon(icon: SvgIcon) {
  styledSpan {
    css {
      child("svg") {
        width = 32.px
        height = 32.px
      }
    }

    val ignore = when(icon) {
      SvgIcon.HELP -> HelpIconWrapper.HelpIcon::class.rClass { }
    }
  }
}
